import React, { Component } from 'react'
import { Container, Draggable } from 'react-smooth-dnd'
import CanvasComponent from './canvasCompodents'

export default class EmailCanvas extends Component {
    state = { isInside: false }
    onDragEnter = () => this.setState({ isInside: true })
    onDragLeave = () => this.setState({ isInside: false })
    onDragDrop = (item) => this.setState({ isInside: false }, () => this.props.onDrop(item))

    onGenerateCanvasItems = (configurations, index) =>
        <Draggable key={index}><CanvasComponent configurations={configurations}/></Draggable>

    render = () => {
        const { canvas } = this.props
        const { isInside } = this.state
        return (
            <div className={`canvas-wrapper${isInside ? ' is-inside':''}`} >
                <Container
                    className={'canvas-containner'}
                    groupName="1"
                    getChildPayload={index => canvas[index]}
                    onDragEnter={this.onDragEnter}
                    onDragLeave={this.onDragLeave}
                    onDrop={this.onDragDrop}
                >
                    { canvas.map(this.onGenerateCanvasItems)}
                </Container>
            </div>
        )
    }
}



