const TITLE = {
    id: 'title',
    config: {
        title: {
            text: 'This is a title',
            style: {
                textAlign: 'center'
            }
        },
        subtitle: {
            text: 'This is a subtitle',
            style: {
                textAlign: 'center'
            }
        },
        style: {
        }
    }
}

const TEXT = {
    id: 'text',
    config: {
        text: 'This is text',
        style: {
            textAlign: 'left'
        }
    }
}

const DIVIDER = {
    id: 'divider'
}

const BUTTON = {
    id: 'button',
    config: {
        buttons: [
            {
                text: 'Button',
                href: 'https://github.paypal.com/aduong',
                style: {
                    backgroundColor: 'red'
                },
            }
        ],
        style:{
            textAlign: 'center',
        },
        limit: 5
    }
}

const IMAGE = {
    id: 'image',
    config: {
        src: 'http://via.placeholder.com/590x200'
    }
}

const IMAGE_TEXT_RIGHT = {
    id: 'imageTextRight',
    data: 'imageTextRight'
}

const IMAGE_TEXT_LEFT = {
    id: 'imageTextLeft',
    data: 'imageTextLeft'
}

const DOUBLE_IMAGE_TEXT = {
    id: 'doubleImageText',
    data: 'doubleImageText'
}

const TRIPLE_IMAGE_TEXT = {
    id: 'tripleImageText',
    data: 'tripleImageText'
}

const LOCATION = {
    id: 'location',
    config: {
        street: '0000 test',
        state: '',
        country: '',
        style: {
            textAlign: 'center'
        }
    }
}

const BLANK = {
    id: 'blank'
}


export const builderSettings = {
    components: [
        TITLE,
        TEXT,
        IMAGE,
        DIVIDER,
        BUTTON,
        LOCATION,
        IMAGE_TEXT_RIGHT,
        IMAGE_TEXT_LEFT,
        DOUBLE_IMAGE_TEXT,
        TRIPLE_IMAGE_TEXT,
        BLANK
    ]
}

export default builderSettings;



