import React from 'react'
import { Container, Draggable } from 'react-smooth-dnd'
import Icon from './optionComponents'

const EmailComponent = ({ configurations, key }) => (
    <Draggable key={key}>
        <div className="draggable-item">
            <Icon id={configurations.id}/>
        </div>
    </Draggable>
)

export const OptionsContainer = ({ components  }) => (
    <div className={'options-wrapper'} >
        <Container groupName="1" behaviour="copy" getChildPayload={index => components[index]} className={'options-container'}>
            {components.map((configurations, index) => <EmailComponent configurations={configurations} key={index} />)}
        </Container>
    </div>
)

export default OptionsContainer



