import React from 'react'
import Title from './title'
import Text from './text'
import Divider from './divider'
import Image from './image'
import ImageTextLeft from './imageTextLeft'
import ImageTextRight from './imageTextRight'
import DoubleImageText from './doubleImageText'
import TripleImageText from './tripleImageText'
import Location from './location'
import Click from './click'
import Blank from './blank'

export const EmailElement = ({ id }) => (
    <div className={'fragment'}>
        {{
            title: <Title />,
            text: <Text />,
            divider: <Divider />,
            image: <Image />,
            blank: <Blank />,
            imageTextLeft: <ImageTextLeft />,
            imageTextRight: <ImageTextRight />,
            doubleImageText: <DoubleImageText />,
            tripleImageText: <TripleImageText />,
            location: <Location />,
            button: <Click />,

        }[id]}
    </div>
)

export default EmailElement



