
import React, { Component } from 'react'
import EmailCanvas from '../containers/canvas'
import { OptionsContainer } from '../containers/options'

export const EmailBuilder = ({ components, canvas, onDrop }) => (
    <div className={'builder-wrapper'} >
        <OptionsContainer components={components}/>
        <EmailCanvas canvas={canvas} onDrop={onDrop}/>
    </div>
)

export default EmailBuilder



